import React from 'react';
import auth from './auth';

const MembersOnly = (props) => {
    return (
        <div>
            <h2>Members Only</h2>
            <p>You must be logged in to see this page</p>
            <button type="button" 
                    onClick={() => {
                        auth.logout(() => props.history.push("/"))
            }}>
                Logout
            </button>
        </div>
    )
}
export default MembersOnly;