import React from 'react';
import {BrowserRouter as Router, Route, NavLink, Switch} from 'react-router-dom';
import Home from './Home';
import PublicPage from './PublicPage';
import MembersOnly from './MembersOnly';
import ProtectedRoute from './ProtectedRoute';
import './App.css';


const App = () => {
  return (
    <div className="App">
      <h1>Protected Route Demo</h1>            
      <Router> 
        <ul className="navbar">
          <li><NavLink exact activeClassName="active" to="/">Home</NavLink></li>
          <li><NavLink exact activeClassName="active" to="/public">Public</NavLink></li>
          <li><NavLink exact activeClassName="active" to="/members">Members</NavLink></li>
        </ul>     
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/public" component={PublicPage} />
          <ProtectedRoute path="/members" component={MembersOnly} />
          <Route path="*" render={() => <h1>Not Found</h1>} />
        </Switch>
      </Router>

    </div>
  );
}

export default App;
