import React from 'react'
import auth from './auth'

const Home = (props) => {
    return (
        <div>
            <h2>Home Page</h2>            
            <button type="button"
                    onClick={
                        () => {
                            auth.login(()=> props.history.push("/members"))
            }}>
                Login
            </button>
        </div>
    )
}

export default Home;